  /*sass --watch src/sass/style.scss:src/index.css*/
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  html,
  body {
    font: normal normal normal 12px/1.5 Verdana, Arial, Sans-Serif;
    background-color: $background;
    color: $fonte;
  }
.gridjs-pages{
  .gridjs-currentPage{
    box-shadow: 0px 0px 15px #008cff;
  }
  button{
  padding: 0px 10px;
}
}
.gridjs-input{
  float: right;
  width: 100%;
  max-width: 310px;
  padding: 5px 5px;
}
  main {
    margin: 0 auto;
    display: flex;
    justify-content: center;
    justify-items: center;
    width: 100%;
    height: 100vh;

    aside {
      width: 20%;
      padding: 20px;
      background-color: $fundo;
      border-radius: 20px 0px 20px 20px;
      ul {
        list-style: none;
      }
      -webkit-animation: bounce-in-left 1.1s linear both;
      animation: bounce-in-left 1.1s linear both;
    }

    .conteudo {
      width: 80%;
      padding: 20px;
      max-height: 100vh;
      -webkit-animation: bounce-in-top 1.1s both;
      animation: bounce-in-top 1.1s both;
    }

    #tituloConteudo {
      position: relative;
      background-color: $fundo;
      padding: 20px;
      width: calc(100% + 40px);
      left: -20px;
      top: -20px;
      border-radius: 0px 20px 20px 0px;
    }

    #entradaMercadoria,
    #saidaMercadoria {
      margin: 0 auto;
      padding: 20px;
      width: 100%;
      max-width: 600px;
      label {
        display: inline-block;
        width: 30%;
        padding: 2px;
      }

      input,select {
        width: 70%;
        padding: 5px;
        border-radius: 10px;
      }

      #entradaEstoque,
      #saidaEstoqueForm {
        position: relative;

        button {
          padding: 2px 10px;
        }

        #Enviar {
          position: absolute;
          right: 0;
          bottom: 0;
        }
      }
    }

    #tabela {
      max-height: 300px;
      padding: 1px;
      overflow: auto;
    }

    table {
      text-align: center;
      border-collapse: collapse;
      position: relative;
      border: 1px solid $fundo;
      margin: 0 auto;

      max-height: 50px;
      width: 100%;
      margin-top: -1px;

      thead {
        tr {
          border: 1px solid $fundo;

          th {
            background-color: $fundo;
            position: sticky;
            top: 0px;
            padding: 2px;
          }
        }
      }

      td {
        padding: 5px;
      }
    }
  }
@-webkit-keyframes bounce-in-left {
  0% {
    -webkit-transform: translateX(-600px);
            transform: translateX(-600px);
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
    opacity: 0;
  }
  38% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    opacity: 1;
  }
  55% {
    -webkit-transform: translateX(-68px);
            transform: translateX(-68px);
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
  }
  72% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
  }
  81% {
    -webkit-transform: translateX(-28px);
            transform: translateX(-28px);
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
  }
  90% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
  }
  95% {
    -webkit-transform: translateX(-8px);
            transform: translateX(-8px);
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
  }
}
